@tailwind components;
@tailwind utilities;

@layer components {
    .ContainerWithTitle {
        @apply p-5 bg-white hover:bg-slate-50 mt-3 mb-3 h-fit;
        box-shadow: 0px 0.3px 2.9px rgb(0 0 0 / 7%),
            0px 0.8px 6.6px rgb(0 0 0 / 5%), 0px 1.3px 11.5px rgb(0 0 0 / 5%),
            0px 2.1px 18.3px rgb(0 0 0 / 4%), 0px 3.3px 28.2px rgb(0 0 0 / 4%),
            0px 5.1px 44px rgb(0 0 0 / 3%), 0px 8.5px 73.1px rgb(0 0 0 / 2%),
            0px 17px 146px rgb(0 0 0 / 2%);
        -webkit-box-shadow: 0px 0.3px 2.9px rgb(0 0 0 / 7%),
            0px 0.8px 6.6px rgb(0 0 0 / 5%), 0px 1.3px 11.5px rgb(0 0 0 / 5%),
            0px 2.1px 18.3px rgb(0 0 0 / 4%), 0px 3.3px 28.2px rgb(0 0 0 / 4%),
            0px 5.1px 44px rgb(0 0 0 / 3%), 0px 8.5px 73.1px rgb(0 0 0 / 2%),
            0px 17px 146px rgb(0 0 0 / 2%);
    }
}
